.header:global(.mdl-layout__header) :global(a:hover),
.header:global(.mdl-layout__header) :global(a.active) {
	background-color: rgba(0, 0, 0, 0.2);
}

.header:global(.mdl-layout__header) :global(a.active) {
	background-color: rgba(0, 0, 0, 0.4);
}

.drawer:global(.mdl-layout__drawer) :global(.active) {
	background-color: #E0E0E0;
}

.footer:global(.mdl-mega-footer) :global(.mdl-mega-footer__left-section) a {
	margin-right: 16px;
	text-decoration: none;
	color: #9e9e9e;
}

.footer:global(.mdl-mega-footer) :global(.mdl-mega-footer__right-section) {
	margin-bottom: 0;
}

@media screen and (min-width: 760px) {
	.footer:global(.mdl-mega-footer) :global(.mdl-mega-footer__left-section),
	.footer:global(.mdl-mega-footer) :global(.mdl-mega-footer__right-section) {
		margin-top: 16px;
		margin-bottom: 16px;
	}
}

@media screen and (min-width: 1025px) {
	.content:global(.mdl-layout__content) {
		width: 984px;
		margin: auto;
	}
}



