.card:global(.mdl-card) {
  width: 100%;
  margin-bottom: 16px;
  min-height: unset;
}

.card:global(.mdl-card) :global(.mdl-card__title) a {
  text-decoration: none;
}

.card:global(.mdl-card) :global(.mdl-card__chips) {
  padding: 0 16px;
}

.card:global(.mdl-card) :global(.mdl-card__supporting-text) {
  width: 100%;
}


